import React, { useEffect, useState } from "react"
import axios from "./axios"
import Search from "./components/Search";
import Display from "./components/Display"

export default function Home(){
    const [getItem, setItem]=useState([])
    const [query, setQuery] = useState('')
    const [result, setResult] = useState([])

    useEffect(()=>{
        axios.getAll().then(cont=>setItem(cont))
    },[])

    const handleQuery = (e) => {
        setQuery(e.target.value)
        const searchQuery = getItem.filter(gi => gi.movieName.toLowerCase().includes(query.toLowerCase()));
        setResult(searchQuery)

    }

    const handleBlur=()=>{
        setQuery("")
        setResult([])
    }
    
    return(
        <div>
           
            <h1 class="py-8 text-center lg:text-5xl text-2xl text-tertiary mb-7">Explore Movies</h1>
            <Search handleQuery={handleQuery} query={query} handleBlur={handleBlur}/>
            <div>
                {result.map((res,index)=>
                    <Display res={res} />
                )}
            </div>
            <div>
                <div class="grid lg:grid-cols-3 grids-cols-1 gap-3  p-10">
                {getItem.map(item=>{
                    const moviename = item.movieName
                    const year =item.yearOfRelease
                    const producer = item.producer
                    const actors = item.actors
                    
                    return ( <div class = "text-center bg-primary flex flex-1 flex-col gap-6  p-10 lg:text-lg   rounded-xl drop-shadow-3xl m- text-tertiary text-lg" >
                        <h1 class="my-10">{moviename}</h1>
                        <h2 class="my-10">{year}</h2>
                        <h2 class="my-10">{producer}</h2>
                        <ul>
                            {actors.map(actor=><li class="list-disc text-center">{actor}</li>)}
                        </ul>
                </div>)
                })}
            </div>
            </div>
           
        </div>
    )
}

