import React from "react";

export default function DisplayResult({res,handleClick,handleDelete}){
    return(
        <div class="text-center bg-primary flex flex-1 flex-col gap-6  p-10 lg:text-lg  m- text-tertiary text-lg">
                        <p>{res.movieName}</p>
                        <p>{res.yearOfRelease}</p>
                        <p>{res.producer}</p>
                        <p>{res.actors}</p>
                        <button type="submit" onClick={(e)=>handleClick(res.id)}>Edit</button>
                        <button type="submit" onClick={(e)=>handleDelete(e,res.id)}>Delete</button>
                    </div>
    )
}